@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");
:root {
  --pc: #007DFC;
  --sc: #F3C342;
  --body-color: #000;
  --gray-bg: #E8F3FF;
  --footer-bg: #272727;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #000;
  --h1toh6: #000;
  --line-light: #E1E1E1;
  --btn-hover: red;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Outfit";
  line-height: 1.5;
  font-size: 24px;
  color: var(--body-color);
}

* {
  font-family: "Outfit";
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: 2.2083333333rem;
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: 1.5rem;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 1.25rem;
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 0.875rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 0.6666666667rem;
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
}

a {
  color: var(--pc);
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.pc {
  color: var(--pc);
}

.sc {
  color: var(--sc);
}

.blue-btn {
  display: inline-flex;
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: var(--white);
  line-height: 1.1666666667rem;
  background-color: var(--pc);
  padding: 0.4166666667rem 1.6666666667rem;
  border-radius: 1.0416666667rem;
  text-decoration: none;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.blue-btn:hover, .blue-btn:focus {
  color: var(--white);
}

.gray-btn {
  display: inline-flex;
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: #000;
  line-height: 1.1666666667rem;
  background-color: var(--white);
  border: 1px solid #000;
  padding: 0.4166666667rem 1.6666666667rem;
  border-radius: 1.0416666667rem;
  text-decoration: none;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
.gray-btn:hover, .gray-btn:focus {
  color: #000;
}

.sec-title {
  font-size: 2rem;
  font-weight: var(--font-m);
  color: #000;
  line-height: 2.4166666667rem;
  text-align: center;
}

.sec-dec {
  font-size: 0.75rem;
  font-weight: var(--font-m);
  color: #717171;
  line-height: 1.1666666667rem;
  text-align: center;
}

@media (max-width: 640px) {
  .sec-title {
    font-size: 1.625rem;
    font-weight: var(--font-m);
    color: #000;
    line-height: 2.25rem;
  }
  .sec-dec {
    font-size: 1rem;
    font-weight: var(--font-m);
    color: #717171;
    line-height: 1.625rem;
  }
  .blue-btn {
    font-size: 1rem;
    font-weight: var(--font-r);
    color: var(--white);
    line-height: 1.625rem;
    padding: 0.5rem 1.5625rem;
    border-radius: 3.125rem;
  }
  .gray-btn {
    font-size: 1rem;
    font-weight: var(--font-r);
    color: #000;
    line-height: 1.625rem;
    padding: 0.5rem 1.5625rem;
    border-radius: 3.125rem;
  }
}
@media (max-width: 1920px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 54.3333333333rem;
  }
  html, body {
    font-size: 28.5px;
  }
}
@media (max-width: 1680px) {
  html, body {
    font-size: 25px;
  }
}
@media (max-width: 1440px) {
  html, body {
    font-size: 24px;
  }
}
@media (max-width: 1399px) {
  html, body {
    font-size: 16px;
  }
}
@media (max-width: 1280px) {
  html, body {
    font-size: 15px;
  }
}
@media (max-width: 1024px) {
  html, body {
    font-size: 12.5px;
  }
}
@media (max-width: 820px) {
  html, body {
    font-size: 10px;
  }
}
@media (max-width: 600px) {
  html, body {
    font-size: 16px;
  }
}
header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99999;
  background: var(--white);
}

.announcement {
  background: var(--pc);
  padding: 0.4166666667rem 0;
}
.announcement-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.announcement-bar i {
  color: var(--white);
  font-size: 0.8333333333rem;
}
.announcement-bar span,
.announcement-bar a {
  display: block;
  text-decoration: unset;
  font-size: 0.5833333333rem;
  font-weight: 300;
  color: var(--white);
  line-height: 0.5833333333rem;
}
.announcement-bar .contact-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2.0833333333rem;
}
.announcement-bar .contact-info .address,
.announcement-bar .contact-info .call {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.625rem;
}
.announcement-bar .social-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.625rem;
}
.announcement-bar .social-info i {
  font-size: 0.8333333333rem;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0.625rem 0;
}
.navbar-brand .logo {
  display: block;
  width: 4.5833333333rem;
  height: auto;
}
.navbar .collapse {
  display: none;
  gap: 2.9166666667rem;
}
.navbar .collapse.show {
  display: flex;
}
.navbar .navbar-toggler {
  padding: 0.25rem 0.75rem;
  font-size: 1.25rem;
  line-height: 1;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}
.navbar .navbar-toggler-icon {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  content: "";
  background: no-repeat center center;
  background-size: 100% 100%;
}
.navbar .navbar-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
.navbar .navbar-nav .nav-item {
  display: list-item;
}
.navbar .navbar-nav .nav-item .nav-link {
  font-size: 0.5833333333rem;
  font-weight: 400;
  color: #4A5568;
  line-height: 1rem;
  display: block;
  padding: 0.5rem 1rem;
  text-decoration: none;
}
.navbar .navbar-nav .nav-item .nav-link.active {
  font-weight: var(--font-b);
  text-decoration: underline;
}
.navbar .search-div {
  position: relative;
}
.navbar .search-div .search {
  font-size: 0.5833333333rem;
  font-weight: 400;
  color: #1E1E1E;
  line-height: 0.5833333333rem;
  border: 1px solid #1E1E1E;
  border-radius: 8px;
  max-width: 8.3333333333rem;
  padding: 0.3333333333rem 0.625rem;
}
.navbar .search-div .fa-magnifying-glass {
  font-size: 0.6666666667rem;
  position: absolute;
  top: 45%;
  right: 0.4166666667rem;
  transform: translate(0, -45%);
}

.banner {
  position: relative;
}
.banner-img {
  width: 100%;
  height: calc(100vh - 4.7916666667rem);
  object-fit: cover;
}
.banner-content {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
}
.banner-content .content-wrapper {
  width: 40%;
}
.banner-content .content-wrapper h1 {
  font-size: 2rem;
  font-weight: var(--font-b);
  color: var(--white);
  line-height: 2.4166666667rem;
  margin-bottom: 0.8333333333rem;
}
.banner-content .content-wrapper p {
  font-size: 0.5833333333rem;
  font-weight: var(--font-r);
  color: var(--white);
  line-height: 1rem;
  margin-bottom: 0.8333333333rem;
}

.features-sec {
  padding: 4.1666666667rem 0;
}
.features-sec .features-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.9166666667rem;
  margin-top: 2.0833333333rem;
}
.features-sec .features-wrapper .features-point {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2.0833333333rem;
}
.features-sec .features-wrapper .features-point > div {
  flex: 1;
}
.features-sec .features-wrapper .features-point:nth-child(2n) {
  flex-direction: row-reverse;
}
.features-sec .features-wrapper .features-point h3 {
  font-size: 1.25rem;
  font-weight: var(--font-r);
  color: #000;
  line-height: 1.6666666667rem;
  margin-bottom: 1.0416666667rem;
}
.features-sec .features-wrapper .features-point p {
  font-size: 0.6666666667rem;
  font-weight: var(--font-r);
  color: #000;
  line-height: 1.0833333333rem;
  margin-bottom: 0.8333333333rem;
}
.features-sec .features-wrapper .features-point .tags-div {
  display: flex;
  align-items: center;
  gap: 0.8333333333rem;
  margin-bottom: 1.25rem;
}
.features-sec .features-wrapper .features-point .tags-div p {
  font-size: 0.5rem;
  font-weight: var(--font-r);
  color: #000;
  line-height: 1rem;
  padding: 0.2083333333rem 0.4166666667rem;
  background: #E1E1E1;
  margin: 0;
}
.features-sec .features-wrapper .features-point .button-wrapper {
  display: flex;
  align-items: center;
  gap: 0.8333333333rem;
}

.slider-section {
  padding: 4.1666666667rem 0;
}
.slider-section .title-wrapper h2 {
  text-align: left;
}
.slider-section .tab-arrow-wrapper {
  margin-top: 2.0833333333rem;
}
.slider-section .tab-arrow-wrapper .tab-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slider-section .tab-arrow-wrapper .tab-wrapper .tab {
  display: flex;
  align-items: center;
  gap: 0.8333333333rem;
}
.slider-section .tab-arrow-wrapper .tab-wrapper .tab a.tab-link {
  font-size: 0.625rem;
  font-weight: var(--font-r);
  color: #231E1C;
  line-height: 0.625rem;
  padding: 0.4166666667rem 0.625rem;
  background: #E8F3FF;
  border-radius: 0.8333333333rem;
  text-decoration: none;
  cursor: pointer;
}
.slider-section .tab-arrow-wrapper .tab-wrapper .tab a.tab-link.active {
  background: var(--pc);
  color: var(--white);
}
.slider-section .tab-arrow-wrapper .arrow-wrapper {
  display: flex;
  align-items: center;
  gap: 0.8333333333rem;
}
.slider-section .tab-arrow-wrapper .arrow-wrapper .arrow-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 0.4166666667rem;
  background: #E8F3FF;
  border-radius: 0.8333333333rem;
  height: 1.5833333333rem;
  width: 1.5833333333rem;
  text-decoration: none;
}
.slider-section .tab-arrow-wrapper .arrow-wrapper .arrow-btn i {
  font-size: 0.875rem;
}
.slider-section .tab-arrow-wrapper .arrow-wrapper .arrow-btn:hover {
  background: var(--pc);
  color: var(--white);
}
.slider-section .slider-slider-wrapper {
  margin-top: 1.4583333333rem;
}
.slider-section .slider-slider-wrapper .slider-box {
  display: none;
}
.slider-section .slider-slider-wrapper .slider-box.active {
  display: block;
}
.slider-section .slider-slider-wrapper .slider-box .slick-slide {
  margin: 0 0.4166666667rem;
}
.slider-section .slider-slider-wrapper .slide-card img {
  width: 100%;
  height: auto;
}
.slider-section .slider-slider-wrapper .slide-card .info-wrapper {
  padding-top: 0.8333333333rem;
}
.slider-section .slider-slider-wrapper .slide-card .info-wrapper h4 {
  font-size: 1rem;
  font-weight: var(--font-r);
  color: #000;
  line-height: 1.4166666667rem;
  margin: 0;
}
.slider-section .slider-slider-wrapper .slide-card .info-wrapper h4 i {
  font-size: 0.5833333333rem;
}
.slider-section .slider-slider-wrapper .slide-card .info-wrapper .location-price-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slider-section .slider-slider-wrapper .slide-card .info-wrapper .location-price-wrapper p {
  font-size: 0.625rem;
  font-weight: var(--font-r);
  color: #8D8D8D;
  line-height: 1.0416666667rem;
  margin: 0;
}

.contact-sec {
  padding: 4.1666666667rem 0;
}
.contact-sec .form-wrapper {
  display: flex;
  padding: 0.4166666667rem;
  margin-top: 2.0833333333rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 0.4166666667rem;
}
.contact-sec .form-wrapper > div {
  display: flex;
  align-items: center;
}
.contact-sec .form-wrapper .contact-info {
  width: 40%;
  flex: 0 0 40%;
  background: var(--pc);
  border-radius: 0.4166666667rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 2.9166666667rem 2.0833333333rem;
}
.contact-sec .form-wrapper .contact-info h4 {
  font-size: 1.1666666667rem;
  font-weight: var(--font-sb);
  color: var(--white);
  line-height: 1.5833333333rem;
  margin-bottom: 0.4166666667rem;
}
.contact-sec .form-wrapper .contact-info p {
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: var(--white);
  line-height: 1.0833333333rem;
  margin-bottom: 0.8333333333rem;
}
.contact-sec .form-wrapper .contact-info .contact-wrapper {
  margin-top: 4.1666666667rem;
}
.contact-sec .form-wrapper .contact-info .contact-wrapper .contact-line {
  display: flex;
  align-items: center;
  gap: 0.8333333333rem;
  margin-bottom: 1.6666666667rem;
}
.contact-sec .form-wrapper .contact-info .contact-wrapper .contact-line i {
  font-size: 1rem;
  color: var(--white);
}
.contact-sec .form-wrapper .contact-info .contact-wrapper .contact-line a,
.contact-sec .form-wrapper .contact-info .contact-wrapper .contact-line span {
  font-size: 0.6666666667rem;
  font-weight: var(--font-r);
  color: var(--white);
  line-height: 1.0833333333rem;
  margin: 0;
  text-decoration: none;
}
.contact-sec .form-wrapper .form-div {
  width: 60%;
  flex: 0 0 60%;
  padding: 2.0833333333rem;
}
.contact-sec .form-wrapper .form-div form {
  width: 100%;
}
.contact-sec .form-wrapper .form-div form .form-controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.8333333333rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .inquiry-box,
.contact-sec .form-wrapper .form-div form .form-controls .message-box,
.contact-sec .form-wrapper .form-div form .form-controls .submit-box {
  grid-column: 1/-1;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-success {
  font-size: 0.8333333333rem;
  font-weight: var(--font-m);
  color: green;
  line-height: 1.0833333333rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-error {
  font-size: 0.8333333333rem;
  font-weight: var(--font-m);
  color: red;
  line-height: 1.0833333333rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box label {
  font-size: 0.5833333333rem;
  font-weight: var(--font-m);
  color: #000;
  line-height: 1.0833333333rem;
  margin: 0;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box input {
  font-size: 0.5833333333rem;
  font-weight: var(--font-m);
  color: #000;
  line-height: 1.0833333333rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box input[type=text], .contact-sec .form-wrapper .form-div form .form-controls .form-box input[type=email], .contact-sec .form-wrapper .form-div form .form-controls .form-box input[type=tel] {
  width: 100%;
  border: unset;
  border-bottom: 1px solid #000;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box input:hover, .contact-sec .form-wrapper .form-div form .form-controls .form-box input:focus {
  outline: none;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box textarea {
  font-size: 0.5833333333rem;
  font-weight: var(--font-m);
  color: #000;
  line-height: 1.0833333333rem;
  width: 100%;
  border: unset;
  border-bottom: 1px solid #000;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box textarea:hover, .contact-sec .form-wrapper .form-div form .form-controls .form-box textarea:focus {
  outline: none;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box.inquiry-box label {
  font-size: 0.6666666667rem;
  font-weight: var(--font-sb);
  color: #000;
  line-height: 1.0833333333rem;
  margin-bottom: 0.4166666667rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box.inquiry-box .inquiry-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.8333333333rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box.inquiry-box .inquiry-list label {
  font-size: 0.5rem;
  font-weight: var(--font-r);
  color: #000;
  line-height: 1.0833333333rem;
  margin: 0;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box.inquiry-box .inquiry-list .type {
  display: flex;
  align-items: center;
  gap: 0.4166666667rem;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box.submit-box {
  text-align: right;
}
.contact-sec .form-wrapper .form-div form .form-controls .form-box.submit-box input {
  border: unset;
  color: var(--white);
}

footer {
  background: #F5FAFF;
  padding: 2.9166666667rem 0 0;
}
footer .logo-div .footer-logo {
  display: block;
  width: 8.3333333333rem;
  height: auto;
  margin: 0 auto;
}
footer .footer-links {
  margin-top: 1.25rem;
}
footer .footer-links ul {
  display: flex;
  justify-content: center;
  gap: 2.9166666667rem;
}
footer .footer-links ul li {
  list-style: none;
}
footer .footer-links ul li a {
  font-size: 0.5833333333rem;
  font-weight: var(--font-r);
  color: #535353;
  line-height: 0.5833333333rem;
  text-decoration: none;
}
footer .footer-links ul li a:hover {
  color: var(--pc);
}
footer .social-div {
  margin-top: 0.8333333333rem;
}
footer .social-div ul {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;
}
footer .social-div ul li {
  list-style: none;
}
footer .social-div ul li a i {
  font-size: 0.8333333333rem;
  color: #000;
}
footer .social-div ul li a:hover i {
  color: var(--pc);
}
footer .copyrights-div {
  margin-top: 1.25rem;
  padding: 0.8333333333rem 0;
  border-top: 1px solid #E5E5E5;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
footer .copyrights-div p {
  font-size: 0.5833333333rem;
  font-weight: var(--font-r);
  color: #535353;
  line-height: 0.5833333333rem;
  margin: 0;
}
footer .copyrights-div .lang-div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.625rem;
}
footer .copyrights-div .lang-div a {
  font-size: 0.5833333333rem;
  font-weight: var(--font-r);
  color: #535353;
  line-height: 0.5833333333rem;
  text-decoration: none;
  padding: 0.4166666667rem 0.8333333333rem;
}
footer .copyrights-div .lang-div a.active {
  color: var(--white);
  background: var(--pc);
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes lineroad {
  to {
    stroke-dashoffset: 0;
  }
}
@media (max-width: 1400px) {
  html,
body {
    font-size: 16px;
  }
}
@media (max-width: 768px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 46.3333333333rem;
  }
}
@media (max-width: 600px) {
  html, body {
    font-size: 16px;
  }
}
@media (min-width: 640px) {
  .navbar-nav {
    flex-direction: row;
  }
  .collapse {
    display: flex !important;
  }
  .navbar-toggler {
    display: none;
  }
}
@media (max-width: 640px) {
  header {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
  }
  .announcement-bar {
    flex-direction: column;
    gap: 0.4166666667rem;
  }
  .announcement-bar a, .announcement-bar span {
    font-size: 0.6875rem;
    font-weight: 300;
    color: var(--white);
    line-height: 0.625rem;
  }
  .navbar-brand .logo {
    width: 7.5rem;
  }
  .navbar .collapse {
    flex-direction: column;
    width: 100%;
    gap: 1.25rem;
  }
  .navbar .navbar-nav {
    flex-direction: column;
  }
  .navbar .navbar-nav .show {
    flex-direction: column;
  }
  .navbar .navbar-nav .nav-item {
    margin-bottom: 0.625rem;
  }
  .navbar .navbar-nav .nav-item .nav-link {
    font-size: 1.125rem;
    font-weight: var(--font-r);
    color: #4a5568;
    line-height: 0.625rem;
  }
  .navbar .search-div {
    display: none;
  }
}
@media (max-width: 640px) {
  .banner {
    height: calc(100vh - 4.4166666667rem);
  }
  .banner-img {
    height: 100%;
  }
  .banner-content {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .banner-content .content-wrapper {
    width: 100%;
    text-align: center;
  }
  .banner-content .content-wrapper p {
    font-size: 0.875rem;
    font-weight: 300;
    color: var(--white);
    line-height: 1.625rem;
  }
  .features-sec {
    padding: 3.125rem 0 1.5625rem;
  }
  .features-sec .features-wrapper .features-point {
    flex-direction: column;
  }
  .features-sec .features-wrapper .features-point:nth-child(2n) {
    flex-direction: column;
  }
  .features-sec .features-wrapper .features-point p {
    font-size: 0.875rem;
    font-weight: 300;
    color: #000;
    line-height: 1.5rem;
  }
  .features-sec .features-wrapper .features-point .tags-div p {
    font-size: 0.875rem;
    font-weight: 300;
    color: #000;
    line-height: 1.5rem;
  }
  .slider-section {
    padding: 1.5625rem 0;
  }
  .slider-section .tab-arrow-wrapper .tab-wrapper .tab {
    overflow-x: scroll;
    overflow-y: hidden;
    padding-bottom: 1.25rem;
  }
  .slider-section .tab-arrow-wrapper .tab-wrapper .tab a.tab-link {
    font-size: 0.875rem;
    font-weight: 300;
    color: #000;
    line-height: 1.5rem;
    text-wrap: nowrap;
    white-space: nowrap;
    padding: 0.625rem 1.25rem;
  }
  .slider-section .tab-arrow-wrapper .arrow-wrapper {
    margin: 0 1.25rem 1.25rem;
  }
  .slider-section .tab-arrow-wrapper .arrow-wrapper .arrow-btn {
    height: 2.5rem;
    width: 2.5rem;
  }
  .slider-section .slider-slider-wrapper .slide-card .info-wrapper h4 {
    font-size: 1.25rem;
    font-weight: var(--font-r);
    color: #000;
    line-height: 1.75rem;
  }
  .slider-section .slider-slider-wrapper .slide-card .info-wrapper .location-price-wrapper p {
    font-size: 0.875rem;
    font-weight: var(--font-r);
    color: #000;
    line-height: 1.5rem;
  }
  .contact-sec {
    padding: 1.5625rem 0 3.125rem;
  }
  .contact-sec .form-wrapper {
    flex-direction: column;
  }
  .contact-sec .form-wrapper .contact-info,
.contact-sec .form-wrapper .form-div {
    width: 100%;
    flex: 0 0 100%;
  }
  .contact-sec .form-wrapper .contact-info {
    padding: 1.875rem;
  }
  .contact-sec .form-wrapper .contact-info h4 {
    font-size: 1.375rem;
    font-weight: var(--font-sb);
    color: var(--white);
    line-height: 1.75rem;
  }
  .contact-sec .form-wrapper .contact-info p {
    font-size: 0.875rem;
    font-weight: 300;
    color: var(--white);
    line-height: 1.5rem;
  }
  .contact-sec .form-wrapper .contact-info .contact-wrapper {
    margin-top: 1.25rem;
  }
  .contact-sec .form-wrapper .contact-info .contact-wrapper .contact-line a,
.contact-sec .form-wrapper .contact-info .contact-wrapper .contact-line span {
    font-size: 0.875rem;
    font-weight: 300;
    color: var(--white);
    line-height: 1.5rem;
  }
  .contact-sec .form-wrapper .form-div {
    padding: 1.875rem;
  }
  .contact-sec .form-wrapper .form-div form .form-controls {
    grid-template-columns: repeat(1, 1fr);
  }
  .contact-sec .form-wrapper .form-div form .form-controls .form-success {
    font-size: 1rem;
    font-weight: var(--font-m);
    color: green;
    line-height: 1.0833333333rem;
    margin-top: 0.625rem;
  }
  .contact-sec .form-wrapper .form-div form .form-controls .form-error {
    font-size: 1rem;
    font-weight: var(--font-m);
    color: red;
    line-height: 1.0833333333rem;
    margin-top: 0.625rem;
  }
  .contact-sec .form-wrapper .form-div form .form-controls .form-box label {
    font-size: 0.875rem;
    font-weight: var(--font-m);
    color: #000;
    line-height: 1.5rem;
  }
  .contact-sec .form-wrapper .form-div form .form-controls .form-box input, .contact-sec .form-wrapper .form-div form .form-controls .form-box textarea {
    font-size: 0.875rem;
    font-weight: var(--font-m);
    color: #000;
    line-height: 1.5rem;
  }
  .contact-sec .form-wrapper .form-div form .form-controls .form-box.inquiry-box label {
    font-size: 0.875rem;
    font-weight: var(--font-m);
    color: #000;
    line-height: 1.5rem;
  }
  .contact-sec .form-wrapper .form-div form .form-controls .form-box.inquiry-box .inquiry-list label {
    font-size: 0.75rem;
    font-weight: var(--font-m);
    color: #000;
    line-height: 1.5rem;
  }
}
footer .footer-links ul {
  flex-wrap: wrap;
  gap: 1.25rem;
}
footer .footer-links ul li a {
  font-size: 0.75rem;
  font-weight: 300;
  color: #000;
  line-height: 1.5rem;
}
footer .social-div ul {
  gap: 1.0416666667rem;
}
footer .social-div ul li a i {
  font-size: 1.5rem;
}
footer .copyrights-div {
  flex-direction: column;
  gap: 1.25rem;
}
footer .copyrights-div .lang-div a {
  font-size: 0.75rem;
  font-weight: 300;
  color: #000;
  line-height: 1.5rem;
  padding: 0.3125rem 1.25rem;
}
footer .copyrights-div p {
  font-size: 0.75rem;
  font-weight: 300;
  color: #000;
  line-height: 1.5rem;
}

